// src/components/RealEstateForm.jsx

import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { addAsset } from "../slices/portfolioSlice";

const RealEstateForm = ({ onClose }) => {
  const dispatch = useDispatch();
  const [isOpen, setIsOpen] = useState(false);

  const [name, setName] = useState("");
  const [price, setPrice] = useState("");
  const [yieldPercent, setYieldPercent] = useState("");
  const [category, setCategory] = useState(" ");
  const [address, setAddress] = useState("");

  //       
  const formatNumber = (value) => {
    const cleanValue = value.replace(/[.,]/g, "");
    if (!cleanValue) return "";
    return cleanValue.replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  };

  //       
  const parseFormattedNumber = (formattedValue) => {
    return parseFloat(formattedValue.replace(/\./g, "")) || 0;
  };

  const handlePriceChange = (e) => {
    const formatted = formatNumber(e.target.value);
    setPrice(formatted);
  };

  const handleYieldPercentChange = (e) => {
    const value = e.target.value.replace(/[^0-9.,]/g, "");
    setYieldPercent(value);
  };

  const handleAddRealEstate = () => {
    if (!name || !price) return;

    const parsedPrice = parseFormattedNumber(price);
    const parsedYield = parseFloat(yieldPercent.replace(",", ".")) || 0;

    //     
    const assetName =
      name || `${category} (${new Date().toLocaleDateString()})`;

    dispatch(
      addAsset({
        type: "realestate",
        name: assetName,
        category,
        address: address || " ",
        quantity: 1,
        value: parsedPrice,
        yieldPercent: parsedYield,
        yearChangePercent: parsedYield,
      })
    );

    //  
    setName("");
    setPrice("");
    setYieldPercent("");
    setAddress("");
    setCategory(" ");
    setIsOpen(false);
    if (onClose) onClose();
  };

  const handleCancel = () => {
    setIsOpen(false);
    if (onClose) onClose();
  };

  if (!isOpen) {
    return (
      <button onClick={() => setIsOpen(true)} className="addButton">
          
      </button>
    );
  }

  return (
    <div className="realestate-form">
      <div className="form-row">
        <input
          type="text"
          placeholder="  *"
          value={name}
          onChange={(e) => setName(e.target.value)}
          required
        />

        <input
          type="text"
          placeholder=" () *"
          value={price}
          onChange={handlePriceChange}
          required
        />

        <input
          type="text"
          placeholder=" (%)"
          value={yieldPercent}
          onChange={handleYieldPercentChange}
        />

        <select value={category} onChange={(e) => setCategory(e.target.value)}>
          <option value=" "> </option>
          <option value=" ">
             
          </option>
          <option value=" "> </option>
          <option value=" ">
             
          </option>
        </select>
      </div>

      <div className="form-buttons">
        <button onClick={handleAddRealEstate}> </button>
        <button onClick={handleCancel}></button>
      </div>
    </div>
  );
};

export default RealEstateForm;
