// src/slices/currencySlice.js

import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";

//       
const currencyToCountry = {
  AUD: "au", // 
  AZN: "az", // 
  GBP: "gb", // 
  BYN: "by", // 
  BGN: "bg", // 
  BRL: "br", // 
  HUF: "hu", // 
  VND: "vn", // 
  HKD: "hk", // 
  GEL: "ge", // 
  DKK: "dk", // 
  AED: "ae", // 
  USD: "us", // 
  EUR: "eu", //  
  EGP: "eg", // 
  INR: "in", // 
  IDR: "id", // 
  KZT: "kz", // 
  CAD: "ca", // 
  QAR: "qa", // 
  KGS: "kg", // 
  CNY: "cn", // 
  MDL: "md", // 
  NZD: "nz", //  
  NOK: "no", // 
  PLN: "pl", // 
  RON: "ro", // 
  XDR: "un", //  (  )
  SGD: "sg", // 
  TJS: "tj", // 
  THB: "th", // 
  TRY: "tr", // 
  TMT: "tm", // 
  UZS: "uz", // 
  UAH: "ua", // 
  CZK: "cz", // 
  SEK: "se", // 
  CHF: "ch", // 
  RSD: "rs", // 
  ZAR: "za", // 
  KRW: "kr", //  
  JPY: "jp", // 
  RUB: "ru", // 
};

export const fetchCurrency = createAsyncThunk(
  "currency/fetchCurrency",
  async () => {
    const res = await fetch("https://www.cbr-xml-daily.ru/daily_json.js");
    const data = await res.json();

    const currencies = Object.entries(data.Valute).map(([code, val]) => {
      const dayChangePercent =
        val.Previous && val.Previous !== 0
          ? +(((val.Value - val.Previous) / val.Previous) * 100).toFixed(2)
          : 0;

      //     
      const countryCode = currencyToCountry[code] || "un";
      const iconUrl = `https://flagsapi.com/${countryCode.toUpperCase()}/flat/64.png`;

      return {
        id: code,
        ticker: code,
        code,
        name: val.Name,
        price: val.Value,
        nominal: val.Nominal,
        value: val.Value,
        yearChangePercent: dayChangePercent,
        type: "currency",
        iconUrl: iconUrl,
        countryCode: countryCode,
      };
    });

    //    
    currencies.unshift({
      id: "RUB",
      ticker: "RUB",
      code: "RUB",
      name: " ",
      price: 1,
      nominal: 1,
      value: 1,
      yearChangePercent: 0,
      type: "currency",
      iconUrl: "https://flagsapi.com/RU/flat/64.png",
      countryCode: "ru",
    });

    return currencies;
  }
);

const initialState = {
  items: [],
  status: "idle",
  error: null,
};

const currencySlice = createSlice({
  name: "currency",
  initialState,
  reducers: {
    clearItems: (state) => {
      state.items = [];
      state.status = "idle";
      state.error = null;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchCurrency.pending, (state) => {
        state.status = "loading";
        state.error = null;
      })
      .addCase(fetchCurrency.fulfilled, (state, action) => {
        state.status = "succeeded";
        state.items = action.payload;
      })
      .addCase(fetchCurrency.rejected, (state, action) => {
        state.status = "failed";
        state.error = action.error.message;
      });
  },
});

export const { clearItems } = currencySlice.actions;
export default currencySlice.reducer;
