// src/components/DepositForm.jsx

import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { addAsset } from "../slices/portfolioSlice";

const DepositForm = ({ onClose }) => {
  const dispatch = useDispatch();
  const [isOpen, setIsOpen] = useState(false);

  const [amount, setAmount] = useState("");
  const [rate, setRate] = useState("");
  const [months, setMonths] = useState("");
  const [depositName, setDepositName] = useState("");

  //       
  const formatNumber = (value) => {
    const cleanValue = value.replace(/[.,]/g, "");
    if (!cleanValue) return "";
    return cleanValue.replace(/\B(?=(\d{3})+(?!\d))/g, ".");
  };

  //       
  const parseFormattedNumber = (formattedValue) => {
    return parseFloat(formattedValue.replace(/\./g, "")) || 0;
  };

  const handleAmountChange = (e) => {
    const formatted = formatNumber(e.target.value);
    setAmount(formatted);
  };

  const handleRateChange = (e) => {
    const value = e.target.value.replace(/[^0-9.,]/g, "");
    setRate(value);
  };

  const handleMonthsChange = (e) => {
    const value = e.target.value.replace(/[^0-9]/g, "");
    setMonths(value);
  };

  const handleAddDeposit = () => {
    if (!amount || !rate || !months) return;

    const parsedAmount = parseFormattedNumber(amount);
    const parsedRate = parseFloat(rate.replace(",", ".")) || 0;
    const parsedMonths = parseInt(months, 10);

    //     
    const name =
      depositName ||
      ` ${parsedRate}%  ${parsedMonths} . (${new Date().toLocaleDateString()})`;

    dispatch(
      addAsset({
        type: "deposit",
        name: name,
        quantity: 1,
        value: parsedAmount, //     
        initialAmount: parsedAmount,
        interest: 0, //   0,    
        rate: parsedRate,
        termMonths: parsedMonths,
        yearChangePercent: parsedRate, //    = 
      })
    );

    //    
    setAmount("");
    setRate("");
    setMonths("");
    setDepositName("");
    setIsOpen(false);
    if (onClose) onClose();
  };

  const handleCancel = () => {
    setIsOpen(false);
    if (onClose) onClose();
  };

  if (!isOpen) {
    return (
      <button onClick={() => setIsOpen(true)} className="addButton">
          
      </button>
    );
  }

  return (
    <div className="deposit-form">
      <div className="form-row">
        <input
          type="text"
          placeholder="  ()"
          value={depositName}
          onChange={(e) => setDepositName(e.target.value)}
        />

        <input
          type="text"
          placeholder=" () *"
          value={amount}
          onChange={handleAmountChange}
          required
        />

        <input
          type="text"
          placeholder="  (%) *"
          value={rate}
          onChange={handleRateChange}
          required
        />

        <input
          type="text"
          placeholder=" (.) *"
          value={months}
          onChange={handleMonthsChange}
          required
        />
      </div>

      <div className="form-buttons">
        <button onClick={handleAddDeposit}> </button>
        <button onClick={handleCancel}></button>
      </div>
    </div>
  );
};

export default DepositForm;
