//src/hooks/usePortfolioOperations.js

import { useCallback } from "react";
import { clearPortfolio } from "../slices/portfolioSlice";

export const usePortfolioOperations = ({
  dispatch,
  savedPortfolioId,
  setSavedPortfolioId,
  setHasUnsavedChanges,
  setOriginalAssetsHash,
  clearPortfolioId,
  showModal,
  createAssetsHash,
}) => {
  //    
  const handleSavePortfolio = useCallback(
    async (portfolioAssets, savePortfolio) => {
      if (portfolioAssets.length === 0) {
        showModal({
          type: "warning",
          title: " ",
          message: "   .",
          showCancel: false,
          icon: "",
        });
        return;
      }

      const result = await savePortfolio(portfolioAssets);

      if (result.success) {
        setSavedPortfolioId(result.portfolioId);
        setHasUnsavedChanges(false);
        setOriginalAssetsHash(createAssetsHash(portfolioAssets));

        try {
          await navigator.clipboard.writeText(result.shareUrl);
          showModal({
            type: "success",
            title: " !",
            message: "      .",
            showCancel: false,
            icon: "",
            autoCloseDelay: 3000,
          });
        } catch {
          showModal({
            type: "info",
            title: " !",
            message: `  : ${result.shareUrl}`,
            showCancel: false,
            icon: "",
          });
        }
      } else {
        showModal({
          type: "error",
          title: " ",
          message: result.error || "   .",
          showCancel: false,
          icon: "",
        });
      }
    },
    [
      setSavedPortfolioId,
      setHasUnsavedChanges,
      setOriginalAssetsHash,
      showModal,
      createAssetsHash,
    ]
  );

  //      
  const handleSaveChanges = useCallback(
    async (portfolioAssets, updatePortfolio, savedPortfolioId) => {
      if (portfolioAssets.length === 0) {
        showModal({
          type: "warning",
          title: " ",
          message: "   .",
          showCancel: false,
          icon: "",
        });
        return;
      }

      if (!savedPortfolioId) {
        return handleSavePortfolio(portfolioAssets, updatePortfolio);
      }

      const result = await updatePortfolio(portfolioAssets, savedPortfolioId);

      if (result.success) {
        setHasUnsavedChanges(false);
        setOriginalAssetsHash(createAssetsHash(portfolioAssets));

        try {
          await navigator.clipboard.writeText(result.shareUrl);
          showModal({
            type: "success",
            title: " !",
            message: "       .",
            showCancel: false,
            icon: "",
            autoCloseDelay: 3000,
          });
        } catch {
          showModal({
            type: "info",
            title: " !",
            message: `   : ${result.shareUrl}`,
            showCancel: false,
            icon: "",
          });
        }
      } else {
        showModal({
          type: "error",
          title: " ",
          message: result.error || "   .",
          showCancel: false,
          icon: "",
        });
      }
    },
    [
      handleSavePortfolio,
      setHasUnsavedChanges,
      setOriginalAssetsHash,
      showModal,
      createAssetsHash,
    ]
  );

  //       
  const openNewPortfolioDialog = useCallback(
    (hasUnsavedChanges, savedPortfolioId) => {
      if (hasUnsavedChanges && savedPortfolioId) {
        showModal({
          type: "confirm",
          title: " ",
          message:
            "    .     ?",
          icon: "",
          onConfirm: () => {
            dispatch(clearPortfolio());
            clearPortfolioId();
            setSavedPortfolioId(null);
            setHasUnsavedChanges(false);
            setOriginalAssetsHash("");

            if (window.history.replaceState) {
              const newUrl = window.location.origin + window.location.pathname;
              window.history.replaceState({}, "", newUrl);
            }

            window.dispatchEvent(new Event("popstate"));

            showModal({
              type: "success",
              title: "  ",
              message: "     .",
              showCancel: false,
              icon: "",
              autoCloseDelay: 2000,
            });
          },
        });
      } else {
        showModal({
          type: "confirm",
          title: "  ?",
          message:
            "   .  ,     ?",
          icon: "",
          onConfirm: () => {
            dispatch(clearPortfolio());
            clearPortfolioId();
            setSavedPortfolioId(null);
            setHasUnsavedChanges(false);
            setOriginalAssetsHash("");

            if (window.history.replaceState) {
              const newUrl = window.location.origin + window.location.pathname;
              window.history.replaceState({}, "", newUrl);
            }

            window.dispatchEvent(new Event("popstate"));

            showModal({
              type: "success",
              title: "  ",
              message: "     .",
              showCancel: false,
              icon: "",
              autoCloseDelay: 2000,
            });
          },
        });
      }
    },
    [
      dispatch,
      clearPortfolioId,
      setSavedPortfolioId,
      setHasUnsavedChanges,
      setOriginalAssetsHash,
      showModal,
    ]
  );

  //   
  const handleCopyLink = useCallback(
    (portfolioAssets, createShareableLink) => {
      const shareLink = createShareableLink(portfolioAssets);
      if (shareLink) {
        navigator.clipboard
          .writeText(shareLink)
          .then(() => {
            showModal({
              type: "success",
              title: " ",
              message: "      .",
              showCancel: false,
              icon: "",
              autoCloseDelay: 2000,
            });
          })
          .catch(() => {
            showModal({
              type: "info",
              title: "  ",
              message: ` : ${shareLink}`,
              showCancel: false,
              icon: "",
            });
          });
      }
    },
    [showModal]
  );

  return {
    handleSavePortfolio,
    handleSaveChanges,
    openNewPortfolioDialog,
    handleCopyLink,
  };
};
